.stars {
  display: flex;
}

.stars > .stars__star {
  background-image: url(/images/star.svg);
  background-repeat: no-repeat;
  background-position: center;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:first-child,
.stars--2 .stars__star:nth-last-child(n + 4),
.stars--3 .stars__star:nth-last-child(n + 3),
.stars--4 .stars__star:nth-last-child(n + 2),
.stars--5 .stars__star:nth-last-child(n) {
  background-image: url(/images/star-active.svg);
}
